@mixin arrowMore {
  background: url("../../images/right_arrow.png") center no-repeat;
  content: "";
  cursor: pointer;
  height: 12px;
  width: 24px;
}

@mixin section {
  box-sizing: border-box;
  height: auto;
  width: 100%;
  padding: 0 10%;
}

@mixin arrow-galery {
  display: block;
  cursor: pointer;
  bottom: 5px;
  width: 35px;
  height: 30px;
  position: absolute;
  background: url("../../images/right_arrow.png") no-repeat center #003355;
  content: "";
}

@mixin menuLinks {
  color: #fff;
  text-decoration: none;
  text-transform: capitalize;
  font-size: 16px;
}
